.todo-list-template {
    background-color: white;
    width: 512px;
    /* 그림자 */
    box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
    margin: 0 auto;
    margin-top: 4rem;
}

.title {
    padding: 2rem;
    font-size: 2.2rem;
    text-align: center;
    font-weight: 100;
    background: #22b8cf;
    color: white;
}


.form-wrapper {
    padding: 1rem;
    border-bottom: 1px solid #22b8cf;
}

.todo-wrapper {
    min-height: 5rem;
}


.palette-wrapper{
    background: #f1f3f5;
    padding: 20px;
}


.palette {
    text-align: center;
}

