.todo-item {
    padding: 1rem;
    display: flex;
    /* 세로 가운데 정렬 */
    align-items: center;
    cursor: pointer;
    transition: all 0.15s;
    user-select: none;
}

.todo-item:hover {
    background: #e3fafc;
}

/* todo-item에 마우스가 있을 때에만 .remove 보이게 하기 */
.todo-item:hover .remove{
    opacity: 1;
}

/* todo-item들 사이에 윗 테두리 */
.todo-item + .todo-item{
    border-top: 1px solid #f1f3f5;
}

.remove {
    margin-right: 1rem;
    color: #e64980;
    font-weight: 600;
    opacity: 0;
}

.todo-text {
    /* check, x를 제외하고 다 채우기 */
    flex: 1;
    word-break: break-all;
}

.checked {
    text-decoration: line-through;
    color: #abd5bd;
}

.check-mark {
    font-size: 1.5rem;
    line-height: 1rem;
    margin-left: 1rem;
    color: #3bc9db;
    font-weight: 800;
}